*/
.CG2-seriesList {
  margin: 0 calc( -50vw + 50% + 8px );

  // @include max-screen( 1072px ) {
  //   margin: 0 calc( -536px + 50% + 12px );
  // }

  @include max-screen( $breakpoint-middle ) {
    margin-left: -18px;
    margin-right: -18px;
  }

  @include print() {
    margin-left: 0;
    margin-right: 0;
  }
}

.CG2-seriesList__inner {
  display: flex;
  flex-wrap: wrap;
  max-width: 1400px;
  margin: 0 auto;

  @include max-screen( $breakpoint-middle ) {
    display: block;
  }
}

.CG2-seriesList__item {
  @include blurry-parent();
  color: #fff;
  box-sizing: border-box;
  width: calc( 50% - 4px );
  padding: 30px 28px;
  margin: 2px;

  @include max-screen( $breakpoint-middle ) {
    width: auto;
    padding: 8px 8px 24px;
    margin: 0 0 2px;
  }

  &.CG2-seriesList__item--fullWidth {
    width: calc( 100% - 4px );
    
    @include max-screen( $breakpoint-middle ) {
      width: auto;
    }
  }
}

.CG2-seriesList__itemHeader {
  display: table;
  width: 100%;
  margin: -18px 0 12px;
  @include max-screen( $breakpoint-middle ) {
    margin: 14px 0 20px;
  }
  .CG2-seriesList__item--fullWidth & {
    margin: 0 0 30px;
    @include max-screen( $breakpoint-middle ) {
      margin: 14px 0 20px;
    }
  }
}

.CG2-seriesList__itemCategory {
  font-family: $fontSerif;
  vertical-align: middle;
  display: table-cell;
  width: 40px;

  @include max-screen( $breakpoint-middle ) {
    font-size: 0;
    vertical-align: top;
    width: 26px;
  }

  .CG2-seriesList__item--fullWidth & {
    width: 150px;

    @include max-screen( $breakpoint-middle ) {
      text-align: center;
      vertical-align: top;
      width: 26px;
    }
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  [class^="CG2-icon-cat"] {
    font-size: 40px;
    line-height: 1;
    position: relative;
    top: 0;
    margin-right: 15px;

    @include max-screen( $breakpoint-middle ) {
      font-size: 26px;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      top: calc( 50% - 20px );
      left: calc( 50% - 20px );
      display: block;
      width: 40px;
      height: 40px;
      border-radius: 20px;
      background: #eee;
      transform: scale( 0 );
      transition: all 0s;
    }
  }

  a:hover [class^="CG2-icon-cat"],
  a:focus [class^="CG2-icon-cat"] {
    @include anim-scaleup();

    &::after {
      opacity: 0;
      transform: scale( 1.4 );
      transition-duration: .4s;
    }
  }
}

.CG2-seriesList__itemTitle {
  font-size: 25px;
  font-weight: bold;
  line-height: 1.4;
  font-family: $fontSerif;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
  height: 2.8em;

  @include max-screen( $breakpoint-middle ) {
    font-size: 20px;
    font-weight: normal;
    height: auto;
    // padding-top: 14px;
  }

  .CG2-seriesList__item--fullWidth & {
    font-size: 30px;
    height: auto;

    @include max-screen( $breakpoint-middle ) {
      font-size: 20px;
    //   display: block;
    //   font-size: 20px;
    }
  }

  a {
    color: inherit;
    text-decoration: none;
    display: block;
  }

  .CG2-seriesList__itemTitleText {
    text-align: left;
    display: inline-block;
    position: relative;

    &:after {
      content: '';
      opacity: 0;
      position: absolute;
      left: 0;
      right: 0;
      display: block;
      height: 1px;
      background: #fff;
      transform: translateY( 2px ) scaleX( 0.1 );
      transition: all .6s cubic-bezier(.3, 1.4, .6, 1 );
    }
  }

  a:hover .CG2-seriesList__itemTitleText:after,
  a:focus .CG2-seriesList__itemTitleText:after {
    opacity: 1;
    transform: scaleX( 1 );
  }
}

.CG2-seriesList__itemUtil {
  text-align: right;
  vertical-align: middle;
  display: table-cell;
  width: 40px;
  padding-left: 10px;

  @include max-screen( $breakpoint-middle ) {
    vertical-align: top;
  }

  .CG2-seriesList__item--fullWidth & {
    width: 150px;

    @include max-screen( $breakpoint-middle ) {
      width: 40px;
    }
  }

  ul {
    font-size: 0;
    list-style: none;
    display: inline-table;
    padding: 0;
  }

  li {
    text-align: center;
    display: table-cell;
    width: 18px;
    height: 18px;
    margin: 0;
    padding: 8px;
    background: rgba( 255, 255, 255, 0.2 );

    @include max-screen( $breakpoint-middle ) {
      width: 14px;
      height: 14px;
    }

    &:first-child {
      &,a {
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
      }
    }

    &:last-child {
      &, a {
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
      }
    }
  }

  a,
  button {
    color: inherit;
    text-decoration: none;
    display: block;
    padding: 8px;
    margin: -8px;
    border: none;
    background: rgba( 255, 255, 255, 0 );
    transition: all .3s;

    &:hover,
    &:focus {
      text-decoration: none;
      background: rgba( 255, 255, 255, 0.2 );

      span[class^="CG2-icon"] {
        opacity: 1;
      }
    }
  }

  span[class^="CG2-icon"] {
    font-size: 18px;
    line-height: 18px;
    opacity: .5;
    position: static;
    display: block;

    @include max-screen( $breakpoint-middle ) {
      font-size: 14px;
      line-height: 14px;
    }
  }
}

button.CG2-seriesList__itemUtilButton--favAdded {
  span[class^="CG2-icon"] {
    color: #ff0;
    opacity: 1;
  }
}

.CG2-seriesList__coverImage {
  @include blurry();
  transition: transform 1s;
  will-change: opacity; // fixing Firefox causing artifacts on unhover

  .CG2-seriesList__item:hover &,
  .CG2-seriesList__item:focus & {
    transform: scale( 1.4, 1.4 );
  }

  // &.CG2-seriesList__coverImage--blurry {
  //   @include blurry--blurry();
  // }
}

.CG2-seriesList__articles {
  position: relative;
  z-index: 1;
  margin-bottom: 15px;
  box-shadow: 0 0 10px rgba( 0, 0, 0, 0.1 );
}

.CG2-seriesList__article {
  font-size: 16px;
  display: table;
  line-height: 1.2;
  width: 100%;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  margin-bottom: -1px;
  background: rgba( 255, 255, 255, 0 );

  &.CG2-seriesList__article--unread {
    font-weight: bold;
    box-shadow: 0 0 10px rgba( 0, 0, 0, 0.1 );
    background: rgba( 255, 255, 255, 0.2 );
  }

  a {
    @include resetLink();
    text-decoration: none;
    display: table-row;
    transition: background .5s;
    
    &:hover,
    &:focus {
      transition: background .2s;
      background: rgba( 255, 255, 255, 0.2 );
    }
  }
}

.CG2-seriesList__articleIcon {
  font-size: 16px;
  display: table-cell;
  vertical-align: middle;
  box-sizing: border-box;
  width: 1px;
  padding: 0 12px 0 18px;

  .CG2-seriesList__item--fullWidth & {
    font-size: 18px;
  }

  @include max-screen( $breakpoint-middle ) {
    font-size: 14px;
    padding: 0 10px;
    
    .CG2-seriesList__item--fullWidth & {
      font-size: 14px;
    }
  }
}

.CG2-seriesList__articleOrder {
  font-size: 16px;
  vertical-align: middle;
  white-space: nowrap;
  display: table-cell;
  width: 1%;
  padding: 15px 15px 15px 0;

  &:first-child {
    padding-left: 20px;
  }

  @include max-screen( $breakpoint-middle ) {
    font-size: 14px;
    padding-right: 10px;

    &:first-child {
      padding-left: 10px;
    }
  }

  .CG2-seriesList__item--fullWidth & {
    font-size: 18px;
    padding-top: 24px;
    padding-bottom: 24px;

    @include max-screen( $breakpoint-middle ) {
      font-size: 14px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
}

.CG2-seriesList__articleTitle {
  font-size: 16px;
  vertical-align: middle;
  display: table-cell;
  padding: 15px 0;

  @include max-screen( $breakpoint-middle ) {
    font-size: 14px;
  }
  
  .CG2-seriesList__item--fullWidth & {
    font-size: 18px;
    padding-top: 24px;
    padding-bottom: 24px;

    @include max-screen( $breakpoint-middle ) {
      font-size: 14px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
}

.CG2-seriesList__articleDate {
  font-size: 12px;
  vertical-align: middle;
  text-align: right;
  display: table-cell;
  white-space: nowrap;
  padding: 15px 20px 15px 5px;

  .CG2-seriesList__item--fullWidth & {
    padding-top: 24px;
    padding-bottom: 24px;

    @include max-screen( $breakpoint-middle ) {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  @include max-screen( $breakpoint-middle ) {
    font-size: 10px;
    padding-right: 10px;
  }
}

.CG2-seriesList__authors {
  position: relative;
  z-index: 1;

  a {
    color: inherit;
    text-decoration: none;
    display: table;
  }
}

.CG2-seriesList__author {
  @extend %author;
  position: relative;
}

a .CG2-seriesList__author:before {
  content: '';
  display: block;
  position: absolute;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: -2px;
  background: rgba( 255, 255, 255, 0 );
  transition: all .5s;
}

a:hover .CG2-seriesList__author:before,
a:focus .CG2-seriesList__author:before {
  background: rgba( 255, 255, 255, 0.2 );
  transition: all .2s;
}

.CG2-seriesList__authorPic {
  @extend %authorPic;
}

.CG2-seriesList__authorName {
  @extend %authorName;

  .CG2-seriesList__item--fullWidth & {
    font-size: 16px;
  }

  @include max-screen( $breakpoint-middle ) {
    .CG2-seriesList__item--fullWidth & {
      font-size: 10px;
    }
  }
}

.CG2-seriesList__authorPicList {
  @extend %authorPicList;
}

.CG2-seriesList__itemMore {
  width: 100%;

  a {
    color: inherit;
    font-family: $fontCode;
    font-size: 28px;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    padding: 20px;
    border-bottom: 1px solid #dbdbdb;

    &:hover,
    &:focus { }

    &:before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      bottom: 0;
      left: -100%;
      margin-left: -60px;
      width: 100%;
      padding: 0 20px;
      transform: skewX( 20deg );
      background: #eee;
      transition: all .3s;
    }

    &:hover:before,
    &:focus:before {
      left: 0;
      margin-left: -20px;
    }
    
    &:after {
      @include icon( down );
      font-size: 20px;
      margin-left: 4px;
    }
  }
}
